export const generalActions = (props) => {
	return {
		airportsData: (data) => {
			props.dispatch({ type: 'AIRPORTS_DATA', data })
		},
		quotesData: (data) => {
			props.dispatch({ type: 'QUOTES_DATA', data })
		},
		toursData: (data) =>{
			props.dispatch({ type: 'TOURS_DATA', data })
		},
		servicesData: (data) => {
			props.dispatch({ type: 'SERVICES_DATA', data })
		},
		changeView: (name) => {
			props.dispatch({ type: 'CHANGE_VIEW', name })
		}
	}
}