import React, { useContext, useState } from 'react'
import { StoreContext } from '../../context/store/storeContext'
import { Table, Card, Button, Modal, notification } from 'antd'
import { deleteTour, getTours } from "../../api"

const { confirm } = Modal


const columns = [
	{
		title: 'Actions',
		dataIndex:'actions',
		key: 'actions',
		fixed: 'left',
		render: (actions) => (
			<div className='actions'>
				<Button key='edit' onClick={actions.edit} type="dashed">
					<i className="fas fa-pencil-alt"></i>
				</Button>
				<Button key='delete' onClick={actions.delete} danger>
					<i className="fas fa-trash"></i>
				</Button>
			</div>
		)
	},
	{
		title: 'Id',
		dataIndex: 'tour_id',
		key: 'id',
	},
	{
		title: 'Name',
		dataIndex: 'name',
		key: 'name',
	},
	{
		title: 'Price',
		dataIndex: 'amount',
		key: 'Amount',
		render: (text) => { return(<span>${text}</span>) }
	},
	{
		title: 'Departure',
		dataIndex: 'dep',
		key: 'departure',
	},
	{
		title: 'Destination',
		dataIndex: 'arr',
		key: 'arrival',
	},
	{
		title: 'Days',
		dataIndex: 'n_days',
		key: 'n_days',
	}
];

const iniState = {
	isLoading: false
}

const ToursList = (props) => {
	const { state, actions } = useContext(StoreContext)
	const { tours, airports } = state.generalStates;
	const [_localState, setState] = useState(iniState)
	const dispatch = actions.generalActions.toursData;
	const dataSource = tours.map(t => {
		t.actions = {
			edit: () => {
				props.onEdit(t)
			},
			delete:  () => {
				showDeleteConfirm(handleDelete, t, _localState, setState, dispatch)
			}
		}
		t.dep = mapAirports(airports, t.departure)
		t.arr = mapAirports(airports, t.arrival)
		return t;
	})
	return (
		<Card className='tours-table' title='Tours'>
			<Table loading={_localState.isLoading} dataSource={dataSource} columns={columns} />
		</Card>
	)
}


async function handleDelete(el, state, setState, dispatch) {
	setState({ ...state, isLoading: true })
	const resp = await deleteTour(el.tour_id);
	console.log(resp, 'tour delete resp');
	const tours = await getTours();
	setState({ ...state, isLoading: false })
	dispatch(tours)
	openNotification('Success delete Tour #' + el.tour_id)
}


function mapAirports(airports, id) {
	let resp = ''
	airports.forEach(a => { if (a.airport_id === id) resp = a.city })
	return resp
}

/**
 * Show confirmation on delete
 * @param {Fucntion} handler 
 * @param  {...any} params 
 */
 function showDeleteConfirm(handler, ...params) {
	confirm({
		title: 'Are you sure delete this Tour?',
		content: 'Will delete this tour permanently',
		okText: 'Yes',
		okType: 'danger',
		cancelText: 'No',
		onOk() {
			handler(...params)
		},
		onCancel() {
			console.log('Cancel');
		},
	});
}

/**
 * Open notification
 * @param {String} title 
 */
 function openNotification (title) {
	notification.open({
		message: title,
		placement: 'bottomRight',
		onClick: () => {
			console.log('Notification Clicked!');
		},
	});
};

export default ToursList