import React, { useState } from 'react'
import ServicesTable from '../ServicesTable'
import { Card, Button } from 'antd'

const iniState = {
	selectedService: {}
}

const InvoicesView = () => {
	const [state, setState] = useState(iniState)
	return (
		<div className='invoice-view'>
			<ServicesTable
				onChange={(row) => {
					setState(Object.assign({}, state, { selectedService: row[0] }))
				}}
			/>
			<Card className='invoice-card' title='Invoice Description'>
				<div className='item'>
					<span>Name</span>
					<p>{ state.selectedService.name || '<name>' }</p>
				</div>
				<div className='item'>
					<span>Phone</span>
					<p>{ state.selectedService.phone || '<phone>' }</p>
				</div>
				<div className='item'>
					<span>Price</span>
					<p>${ state.selectedService.amount || '<phone>' }</p>
				</div>
				<Button disabled={ !state.selectedService.name } type='primary'>
					Print
					<i className="fas fa-print"></i>
				</Button>
			</Card>
		</div>
	)
}

export default InvoicesView