import React, { useState } from 'react'
import ToursList from "../ToursList";
import TourFrom from '../TourForm';

const iniState = {
	data: {}
}

const ToursView = () => {
	const [state, setState] = useState(iniState)
	console.log(state);
	return (
		<div className='tours-view'>
			<ToursList
				onEdit={(el) => { setState({ ...state, data: el }) }}
			/>
			<TourFrom data={state.data} />
		</div>
	)
}

export default ToursView