import React, { useContext, useState } from 'react';
import { StoreContext } from '../../context/store/storeContext';
import { Card, Select, Descriptions, Button, notification } from 'antd';
import EditQuoteModal from '../EditQuoteModal'
import { getPendindQuotes, createQuote } from '../../api';

const { Option } = Select

const iniState = {
	selectValue: 0,
	isVisible: false,
	tourSelected: {

	}
}

const ToursTable = () => {
	const { state, actions } = useContext(StoreContext)
	const [_localState, setState] = useState(iniState)
	const { tours } = state.generalStates
	const { quotesData } = actions.generalActions
	return (
		<Card 
		title='Create Quote From tour'
		className='tour-card'
		extra={<i className="fas fa-expand-arrows-alt"></i>}
		>
			<Select 
				value={ _localState.selectValue }
				onChange={(val) => { 
					let selected = 0;
					tours.forEach(t => { if(t.tour_id === val) { selected = t } });
					setState(Object.assign({}, _localState, { selectValue: val, tourSelected: selected }))
				 }}
			>
				<Option value={0} key='default'>Select Tour</Option>
				{
					tours.map(t => {
						return (
						<Option key={t.tour_id} value={t.tour_id}>
							{t.name}
						</Option>
						)
					} )
				}
			</Select>
			<Descriptions
				title='Tour Description'
			>
					<Descriptions.Item label="Name">
						{
							_localState.tourSelected.name || 'Name'
						}
					</Descriptions.Item>
					<Descriptions.Item label="Departure">
						{
							_localState.tourSelected.departure || 'departure'
						}
					</Descriptions.Item>
					<Descriptions.Item label="Destination">
						{
							_localState.tourSelected.arrival || 'arrival'
						}
					</Descriptions.Item>
					<Descriptions.Item label="Price">
						${
							_localState.tourSelected.amount || 'amount'
						}
					</Descriptions.Item>
				</Descriptions>
				<Button 
					type='primary'
					disabled={ _localState.selectValue === 0 }
					onClick={() => {
						setState(Object.assign({}, _localState, { isVisible: true }))
					}}
				>
					Create Quote from tour
				</Button>
				<EditQuoteModal
					visible={_localState.isVisible}
					data={ 
						Object.assign(
							{},
							_localState.tourSelected,
							{ name: '', description: _localState.tourSelected.name })
						}
					onSubmit={(val) => {
						console.log(val);
						setState(iniState)
						createNewQuote(val, quotesData);
					}}
					onCancel={()=>{
						setState(Object.assign({}, _localState, { isVisible: false }))
					}}
				/>
		</Card>
	)
}

async function createNewQuote(data, dispatch) {
	const resp = await createQuote(data);
	if (resp === 200) {
		const quotes = await getPendindQuotes();
		dispatch(quotes)
		openNotification('Success Quote Created')
	} else {
		openNotification('Error writing, make sure to fill all the fields')
	}
}

/**
 * Open notification
 * @param {String} title 
 */
 function openNotification (title) {
	notification.open({
		message: title,
		placement: 'bottomRight',
		onClick: () => {
			console.log('Notification Clicked!');
		},
	});
};


export default ToursTable;