import React , { useContext, useEffect } from "react";
import { StoreContext } from './context/store/storeContext';
import './sass/index.scss';
import MainHeader from './components/MainHeader/index';
import SideMenu from './components/SideMenu';
import Router from './components/Router';
import "antd/dist/antd.css";
import { getAirports, getPendindQuotes, getServices, getTours } from './api';

const App = () => {
	const { actions, state } = useContext(StoreContext);
	const { activeView } = state.generalStates;
	useEffect(() => {
		getData(actions.generalActions);
	}, [])
	return (
		<div className="dashboard-wrapper">
			<MainHeader></MainHeader>
			<SideMenu></SideMenu>
			<Router name={activeView}></Router>
		</div>
	);
};
/**
 * First fetch for the data
 */
async function getData(generalActions) {
	const {
		airportsData,
		quotesData,
		toursData,
		servicesData
	} = generalActions
	const airports = await getAirports()
	if (airports) airportsData(airports)
	const quotes = await getPendindQuotes()
	if (quotes) quotesData(quotes)
	const tours = await getTours()
	if (tours) toursData(tours)
	const services = await getServices()
	if (services) servicesData(services)
}

export default App
