import React, { useState, useContext, useEffect } from 'react';
import { StoreContext } from '../../context/store/storeContext'
import { Card, Input, Select, Button, notification } from "antd";
import { createTour, getTours, updateTour } from '../../api';


const { Option } = Select

const iniState = {
	name: '',
	n_days: '',
	arrival: 0,
	departure: 0,
	amount: '',
	isLoading: false
}

let oldProps = {}

const TourForm = (props) => {
	const [_localState, setState] = useState(iniState)
	const { state, actions } = useContext(StoreContext)
	const dispatch = actions.generalActions.toursData;
	const { airports } = state.generalStates
	const isReady = isBtnReady(_localState)
	useEffect(() => {
		if(JSON.stringify(props.data) !== '{}' &&
			JSON.stringify(props.data) !== JSON.stringify(oldProps)
			) {
				oldProps = props.data
				setState({ ..._localState, ...props.data, isEdit: true })
		}
	})
	return (
		<Card
		 className='create-tour'
		 title= { 'Create new Tour' }
		 >
			<Input
				key='name'
				name='name'
				placeholder='name'
				disabled={_localState.isLoading}
				onChange={ (ev) => { mapInputs(ev, _localState, setState) } }
				value={_localState.name}
			/>
			<Input
				key='n_day'
				name='n_days'
				placeholder='Days'
				disabled={_localState.isLoading}
				onChange={ (ev) => { mapInputs(ev, _localState, setState) } }
				value={_localState.n_days}
			/>
			<Select
				key='departure'
				name='departure'
				disabled={_localState.isLoading}
				onChange={ (val) => { setState({..._localState, departure: val}) } }
				value={_localState.departure}
			>
				<Option value={0} key='0'> Departure </Option>
				{
					airports.map(a => {
						return (<Option value={a.airport_id} key={a.airport_id}>{ a.name }</Option>)
					})
				}
			</Select>
			<Select
				key='arrival'
				name='arrival'
				disabled={_localState.isLoading}
				onChange={ (val) => { setState({..._localState, arrival: val}) } }
				value={_localState.arrival}
			>
				<Option value={0} key='0'> Destination </Option>
				{
					airports.map(a => {
						return (<Option value={a.airport_id} key={a.airport_id}>{ a.name }</Option>)
					})
				}
			</Select>
			<Input
				key='amount'
				name='amount'
				placeholder='amount'
				disabled={_localState.isLoading}
				onChange={ (ev) => { mapInputs(ev, _localState, setState) } }
				value={ '$' + _localState.amount }
			/>
			<div></div>
			<Button disabled={_localState.isLoading} onClick={() => { setState(iniState) }}>Clear</Button>
			<Button
				onClick={() => { createNewTour(_localState, setState, dispatch) }}
				type='primary'
				disabled={!isReady || _localState.isLoading}
			>
				{ !_localState.isEdit ? 'Create' : 'Confirm Edit' }
			</Button>
		</Card>
	)
}
/**
 * Util function ton enable button
 */
function isBtnReady(state) {
	if (
		state.name &&
		state.n_days &&
		state.arrival &&
		state.departure &&
		state.amount
		) return true
		return false
}

async function createNewTour(state, setState, dispatch) {
	setState({ ...state, isLoading: true })
	let resp
	if(!state.isEdit) {
		resp = await createTour(state)
	} else {
		resp = await updateTour(state.tour_id, state)
	}
	console.log(resp);
	const tours = await getTours()
	dispatch(tours)
	openNotification(!state.isEdit ? 'Success, new Tour added' : 'Success, Tour edited')
	setState(iniState)
}

/**
 * Map input values to local state
 * */
 function mapInputs(ev, state, setState) {
	const name = ev.target.name
	const data = ev.nativeEvent.data
	if(data !== null) {
		setState(Object.assign({}, state, { [name]: state[name] + data }))
	} else if(ev.nativeEvent.inputType === 'deleteContentBackward') {
		let val = state[name];
		val = val.split(''); val.pop(); val =val.join('');
		setState(Object.assign({}, state, { [name]: val }))
	}
}

/**
 * Open notification
 * @param {String} title 
 */
 function openNotification (title) {
	notification.open({
		message: title,
		placement: 'bottomRight',
		onClick: () => {
			console.log('Notification Clicked!');
		},
	});
};


export default TourForm