import React, { useContext } from 'react'
import { Card, Table } from 'antd'
import { StoreContext } from '../../context/store/storeContext';


const columns = [
	{
		title: 'Id',
		dataIndex: 'quote_id',
		key: 'id',
	},
	{
		title: 'Name',
		dataIndex: 'name',
		key: 'name',
	},
	{
		title: 'Phone',
		dataIndex: 'phone',
		key: 'pone',
	},
	{
		title: 'Depart Date',
		dataIndex: 'departDate',
		key: 'depart_date',
	},
	{
		title: 'Return Date',
		dataIndex: 'returnDate',
		key: 'return_date',
	},
	{
		title: 'People',
		dataIndex: 'n_people',
		key: 'n_people',
	},
	{
		title: 'Transportation',
		dataIndex: 'transportation',
		key: 'transportation',
	},
	{
		title: 'Amount',
		dataIndex: 'Amount',
		key: 'Amount',
	},
	{
		title: 'Departure',
		dataIndex: 'departure',
		key: 'departure',
	},
	{
		title: 'Arrival',
		dataIndex: 'arrival',
		key: 'arrival',
	},
	{
		title: 'Description',
		dataIndex: 'description',
		key: 'description',
	}
];


const ServicesTable = (props) => {
	const { state } = useContext(StoreContext);
	const dataSource = state.generalStates.services.map(el => {
		el.departDate = getFormatedDate(el.depart_date)
		el.returnDate = getFormatedDate(el.return_date)
		el.key = el.quote_id
		return el
	})
	return (
		<Card className='services-table' title='Completed Services'>
			<Table
				rowSelection={{
					type: 'radio',
					onChange: (selectedRowKeys, selectedRows) => {
						console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
						props.onChange(selectedRows)
					}
				}}
				dataSource={dataSource}
				columns={columns}
			/>
		</Card>
	)
}

/**
 * get short date
 * @param {String} strDate 
 * @returns 
 */
 function getFormatedDate(strDate) {
	const date = new Date(strDate);
	return `${date.getMonth()}/${date.getDay()}/${date.getFullYear()}`
}



export default ServicesTable