export const generalStates = {
	airports: [],
	quotes: [],
	tours: [],
	services: [],
	activeView: 'home'
}

export const generalReducer = (state, action) => {
	switch (action.type) {
		case 'AIRPORTS_DATA':
			return {
				...state,
				airports: action.data
			}
		case 'QUOTES_DATA':
			return {
				...state,
				quotes: action.data
			}
		case 'TOURS_DATA':
			return {
				...state,
				tours: action.data
			}
		case 'CHANGE_VIEW':
			return {
				...state,
				activeView: action.name
			}
		case 'SERVICES_DATA':
			return {
				...state,
				services: action.data
			}
		default:
			throw new Error("Unexpected action");
	}
};