import React, { useContext } from 'react';
import { StoreContext } from '../../context/store/storeContext';


const tabs = [
	{
		icon: 'fas fa-home',
		name: 'Home'
	},
	{
		icon: 'fas fa-dollar-sign',
		name: 'Quotes'
	},
	{
		icon: 'fas fa-th-list',
		name: 'Leaders'
	},
	{
		icon: 'fas fa-paper-plane',
		name: 'Tours'
	}
]

const tabs2 = [
	{
		icon: 'fas fa-sticky-note',
		name: 'Invoices'
	},
	{
		icon: 'fas fa-chart-line',
		name: 'Analytics'
	},
	{
		icon: 'fas fa-users',
		name: 'Team'
	},
	{
		icon: 'fas fa-cog',
		name: 'Admin'
	}
	,
	{
		icon: 'far fa-life-ring',
		name: 'Support'
	}
]

const MainHeader = () => {
	const { state, actions } = useContext(StoreContext);
	return (
		<div className='side-menu-wrapper'>
			<div>
				{
					tabs.map(tab => {
						return getItem(tab.icon, tab.name, state, actions.generalActions.changeView)
					})
				}
			</div>
			<div>
				{
					tabs2.map(tab => {
						return getItem(tab.icon, tab.name, state, actions.generalActions.changeView)
					})
				}
			</div>
			<div className='menu-footer'>
				<span>alright received</span>
				<span>by Wetbat 2020</span>
			</div>
		</div>
	)
}

function getItem(icon, name, state, dispatch) {
	const isActive = name.toLowerCase() === state.generalStates.activeView;
	const classAdd = isActive ? ' active' : '';
	return (
		<div 
		onClick={() => {
			if(!isActive) dispatch(name.toLowerCase())
		}}
		className={'menu-item' + classAdd}
		>
			<i className={icon}></i>
			<span>{name}</span>
		</div>
	)
}

export default MainHeader