import React from 'react'
import Dashboard from '../Dashboard'
import QuotesView from '../QuotesView'
import InvoicesView from '../InvoicesView'
import ToursView from '../ToursView'


const Router = (props) => {
	return (
		<div className='router-box'>
			{ getActiveView(props.name) }
		</div>
	)
}

function getActiveView(name) {
	switch (name) {
		case 'home':
			return (<Dashboard/>)
		case 'quotes':
			return (<QuotesView/>)
		case 'invoices':
			return (<InvoicesView/>)
		case 'tours':
			return (<ToursView/>)
		default:
			return (<Dashboard/>)
	}
}

export default Router